header {
  max-width: 1308px;
  margin: 0 auto;
  width: 100%;
  height: 663px;
  background-image: url("../images/background-header.png");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  padding-top: 270px;
  color: #FFFFFF; }
  header h1 {
    font-size: 6em;
    font-family: "Open Sans";
    font-style: normal;
    font-variant: normal;
    font-weight: 400;
    text-transform: uppercase;
    padding-bottom: 35px; }
    header h1 span {
      font-weight: 600; }
  header hr {
    margin: 0 auto;
    width: 14.5em;
    display: block;
    height: 1px;
    border: 0;
    border-top: 2px solid #da491c; }
  header p {
    padding-top: 40px;
    font-size: 1.6em;
    line-height: 2.2em; }

nav {
  max-width: 1308px;
  margin: 0 auto;
  left: 0px;
  right: 0px;
  top: 0;
  position: fixed;
  width: 100%;
  height: 60px;
  color: #FFFFFF;
  background-color: rgba(0, 0, 0, 0.7);
  animation: opacity 2s; }

@keyframes opacity {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
  nav .logo {
    width: 50%;
    float: left;
    padding: 20px 70px; }
    nav .logo img {
      display: inline-block; }
  nav .menu {
    width: 50%;
    float: left; }

nav:before,
nav:after {
  content: "";
  display: table;
  clear: both; }

@media screen and (max-width: 740px) {
  nav {
    height: 120px; }
    nav .logo {
      width: 100%;
      clear: both;
      text-align: center; }
    nav .menu {
      width: 100%;
      clear: both; } }

section {
  max-width: 1308px;
  margin: 0 auto;
  height: 1000px;
  font-size: 36px; }

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility !important;
  font-size: 62.5%; }

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }
